@import url(https://cdnjs.cloudflare.com/ajax/libs/slick-carousel/1.6.0/slick.min.css);
@import url(https://cdnjs.cloudflare.com/ajax/libs/slick-carousel/1.6.0/slick-theme.min.css);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{width:100%;font-family:"Roboto",sans-serif;background:url(/cv/static/media/background.be8d2259.png);background-position:center}.logo,.rounded{margin:auto;display:block;animation:fadein 2s}.logo{margin-top:100px}.rounded{margin-top:20px}h1,h2,h3,p{font-weight:100;color:#fff;animation:fadein 1s}h2{margin-bottom:40px}h3{margin-top:0px;font-weight:500}p{font-weight:normal}a{font-weight:bold;color:#fff}.bar-footer{position:fixed;bottom:0px}.rounded{margin:auto;margin-top:100px;display:block;width:150px;height:150px;border-radius:75px;-webkit-border-radius:75px;-moz-border-radius:75px;background:url(/cv/static/media/moi.b6021be0.png) no-repeat}.card{animation:fadein 1s}i{font-size:20px}.item-text-wrap a{margin-top:20px}.pane{flex:1 1;flex-direction:column}.pane .block{margin-left:auto;margin-right:auto;flex:1 1;flex-direction:column;padding:10px;max-width:720px}.pane .block.identity{text-align:center}@keyframes fadein{from{opacity:0}to{opacity:1}}
figure{-webkit-margin-before:0px;margin-block-start:0px;-webkit-margin-after:0px;margin-block-end:0px;-webkit-margin-start:0px;margin-inline-start:0px;-webkit-margin-end:0px;margin-inline-end:0px;background-color:#444;height:240px}figure blockquote{margin:0}figure blockquote p{padding:15px;border-radius:5px}figure figcaption{text-align:right;padding:5px;color:#fff}.slick-slider{margin-top:-25px}.slick-slide{height:250px}.slick-dots{bottom:0px}
